import React from 'react';
import logo from '../../i-logo.svg';
import '../../App.css';

function MainPage(){
  return(
    <div className="App">
    <header className="App-header">
      <img src={logo} className="App-logo" alt="logo" />
      <p>
        Mi Pinche React App
      </p>
      <a
        className="App-link"
        href="https://www.becker.com/"
        target="_blank"
        rel="noopener noreferrer"
      >
        Vamos...
      </a>
    </header>
  </div>
  )
}

export default MainPage;